a {
  text-decoration: none;
  color: #363636;
}
body {
  background-color: #f2f2f2;
  color: #363636;
  overflow: hidden;
  position: absolute;
  width: 100vw;
  height: 200vh;
  margin: 0px;
  padding: 0;
}
.title {
  position: relative;
  width: 80vw;
  height: 80vh;
}
.title_margin {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
}
.logo {
  position: absolute;
  top: 50vh;
  left: 5%;
  transform: translateY(-25vh);
  display: inline-block;
  width: 50vh;
}
.title_description {
  position: absolute;
  bottom: 3vh;
  right: 5%;
  display: inline-block;
  text-align: right;
}
.title_description .title_date {
  display: block;
  font-weight: bold;
  font-size: 200%;
  margin-bottom: 3%;
}
.title_description .title_whoweare_japanese {
  display: block;
  font-weight: bold;
  margin-bottom: 1%;
}
.title_description .title_whoweare_english {
  display: block;
}
