.page2_margin {
  width: 100vw;
  height: 100vh;
}
.page2 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 5%;
  font-size: 100%;
}
.greetings {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}
.greetings .greetings_japanese {
  display: flex;
  flex-direction: column;
  margin-left: 5%;
  margin-right: 3%;
  margin-bottom: 5%;
  font-size: 60%;
  font-weight: bold;
  line-height: 200%;
  letter-spacing: 1px;
}
.greetings .greetings_english {
  display: flex;
  flex-direction: column;
  margin-right: 5%;
  margin-bottom: 5%;
  font-size: 60%;
  font-weight: bold;
  line-height: 200%;
}
.greetings .greetings_japanese .greetings_japanese_title {
  text-align: right;
  font-size: 150%;
  margin-bottom: 2%;
  font-weight: bold;
}
.greetings .greetings_english .greetings_english_title {
  font-size: 150%;
  margin-bottom: 2%;
  font-weight: bold;
}
.sketch_yam {
  width: 40%;
  margin: auto;
  margin-bottom: 5%;
}
.button_gallery {
  background-color: #777777;
  color: white;
  width: 200px;
  height: 50px;
  margin: auto;
  text-align: center;
  line-height: 50px;
}
